/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

html {
  font-family: Montserrat, sans-serif;
  color: #000000;
  font-size: 1em;
  line-height: 1.4;
  height: 100%;
}

body {
  height: 100%;
}

/*
 * Remove text-shadow in selection highlight:
 * https://twitter.com/miketaylr/status/12228805301
 *
 * Vendor-prefixed and regular ::selection selectors cannot be combined:
 * https://stackoverflow.com/a/16982510/7133471
 *
 * Customize the background color to match your design.
 */

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

/*
 * A better looking default horizontal rule
 */

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

/*
 * Remove the gap between audio, canvas, iframes,
 * images, videos and the bottom of their containers:
 * https://github.com/h5bp/html5-boilerplate/issues/440
 */

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

/*
 * Remove default fieldset styles.
 */

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

/*
 * Allow only vertical resizing of textareas.
 */

textarea {
  resize: vertical;
}

/* ==========================================================================
   Author's custom styles
   ========================================================================== */

.head {
  font-weight: bold;
}

.subhead {
  font-weight: 600;
}

.teal {
  color: #03A89C;
}

.mid-grey {
  color: #B3B3B3;
}

.light-grey {
  color: #F2F2F2;
}

.center {
  text-align: center;
}

h1 {
  font-weight: bold;
  font-size: 40px;
  margin: 0;
}

h2 {
  margin: 0;
  font-size: 26px;
  font-weight: bold;
}

h3 {
  font-weight: bold;
  margin: 0;
  font-size: 24px;
}

h4 {
  font-weight: bold;
  font-size: 20px;
  line-height: 26px;
  margin: 1em 0;
}

h6 {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
}

p {
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}

p.large {
  font-size: 15px;
  line-height: 22px;
}

.inline-block {
  display: inline-block;
}

button {
  background-color: #03A89C;
  border-radius: 8px;
  color: #ffffff;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  border: none;
  cursor: pointer;
  padding: 1vh 1.5vw;
  margin-top: 2vh;
}

button:hover {
  background-color: rgba(3,168,156,0.7);
}

main {
  width: -webkit-fill-available;
  height: -webkit-fill-available;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 120px;
}

nav {
  height: 54px;
  background-color: #F2F2F2;
}

section.banner {
  background-image: url("../img/teal-underwater-light.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  height: 765px;
}

section.banner article {
  padding: 4vh 15vw 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(255,255,255,0.7);
}

section.adult-use {
  padding: 2vh 31vw 3vh;
  background-color: #F2F2F2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

section.markets {
  padding: 5vh 10vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.markets > p {
  padding: 0 5vw;
}

span.cards {
  display: flex;
  gap: 16px;
  padding: 5vh 2vw;
  width: -webkit-fill-available;
  flex-wrap: wrap;
}

span.cards article.card {
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  background-color: #F2F2F2;
  padding: 0 2vw 2vw;
  width: 27%;
  position: relative;
}

span.cards article.card.white {
  background-color: #ffffff;
  padding-bottom: 5vw;
}

.card.white .header {
  height: 33%;
  min-height: 33%;
}

.card.white h4 {
  margin-bottom: 0;
}

.card .image {
  width: -webkit-fill-available;
  margin: 0 -2vw 2vw;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 23vh;
  min-height: 23vh;
  max-height: 23vh;
}

.card .subhead {
  margin-top: 12px;
}

.card.white .subhead {
  margin-bottom: 24px;
}

.card ul {
  margin: 0;
  padding-inline-start: 16px;
  list-style: none;
}

.card ul li::before {
  color: #03a89c;
  content: "\2022";
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

svg.icon {
  width: 5vw;
  position: absolute;
  right: 2vw;
  bottom: 8vw;
}

svg.icon.smaller {
  width: 4vw;
}

svg.arrow {
  width: 1vw;
  cursor: pointer;
  position: absolute;
  right: 2vw;
  bottom: 2vw;
}

section.difference {
  background-color: #F2F2F2;
  padding: 6vh 10vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

section.talk {
  padding: 6vh 10vw 8vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

section.talk.partners {
  background-color: #F2F2F2;
}

section.talk img {
  width: 63vw;
}

footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #03a89c;
  padding: 5vh 10vw;
}

footer svg.logo {
  width: 21vw;
  margin-bottom: 6vh;
}

footer .socials {
  display: flex;
  align-items: center;
  margin-bottom: 5vh;
}

footer .socials .icon {
  border-radius: 50%;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5vw;
  height: 2.5vw;
  margin: 0 1vw;
  cursor: pointer;
}

footer .socials .icon:hover {
  background-color: rgba(0,0,0,0.5);
}

footer .socials .icon svg {
  width: 0.75vw;
}

footer .socials .icon svg.larger {
  width: 1.5vw;
}

/* ==========================================================================
   Helper classes
   ========================================================================== */

/*
 * Hide visually and from screen readers
 */

.hidden,
[hidden] {
  display: none !important;
}

/*
 * Hide only visually, but have it available for screen readers:
 * https://snook.ca/archives/html_and_css/hiding-content-for-accessibility
 *
 * 1. For long content, line feeds are not interpreted as spaces and small width
 *    causes content to wrap 1 word per line:
 *    https://medium.com/@jessebeach/beware-smushed-off-screen-accessible-text-5952a4c2cbfe
 */

.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
  /* 1 */
}

/*
 * Extends the .sr-only class to allow the element
 * to be focusable when navigated to via the keyboard:
 * https://www.drupal.org/node/897638
 */

.sr-only.focusable:active,
.sr-only.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  white-space: inherit;
  width: auto;
}

/*
 * Hide visually and from screen readers, but maintain layout
 */

.invisible {
  visibility: hidden;
}

/*
 * Clearfix: contain floats
 *
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *    `contenteditable` attribute is included anywhere else in the document.
 *    Otherwise it causes space to appear at the top and bottom of elements
 *    that receive the `clearfix` class.
 * 2. The use of `table` rather than `block` is only necessary if using
 *    `:before` to contain the top-margins of child elements.
 */

.clearfix::before,
.clearfix::after {
  content: " ";
  display: table;
}

.clearfix::after {
  clear: both;
}

/* ==========================================================================
   EXAMPLE Media Queries for Responsive Design.
   These examples override the primary ('mobile first') styles.
   Modify as content requires.
   ========================================================================== */

@media only screen and (min-width: 35em) {
  /* Style adjustments for viewports that meet the condition */
}

@media print,
  (-webkit-min-device-pixel-ratio: 1.25),
  (min-resolution: 1.25dppx),
  (min-resolution: 120dpi) {
  /* Style adjustments for high resolution devices */
}

/* ==========================================================================
   Print styles.
   Inlined to avoid the additional HTTP request:
   https://www.phpied.com/delay-loading-your-print-css/
   ========================================================================== */

@media print {
  *,
  *::before,
  *::after {
    background: #fff !important;
    color: #000 !important;
    /* Black prints faster */
    box-shadow: none !important;
    text-shadow: none !important;
  }

  a,
  a:visited {
    text-decoration: underline;
  }

  a[href]::after {
    content: " (" attr(href) ")";
  }

  abbr[title]::after {
    content: " (" attr(title) ")";
  }

  /*
   * Don't show links that are fragment identifiers,
   * or use the `javascript:` pseudo protocol
   */
  a[href^="#"]::after,
  a[href^="javascript:"]::after {
    content: "";
  }

  pre {
    white-space: pre-wrap !important;
  }

  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }

  /*
   * Printing Tables:
   * https://web.archive.org/web/20180815150934/http://css-discuss.incutio.com/wiki/Printing_Tables
   */
  thead {
    display: table-header-group;
  }

  tr,
  img {
    page-break-inside: avoid;
  }

  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }

  h2,
  h3 {
    page-break-after: avoid;
  }
}

